html,
body {
  max-width: 100%;
  min-width: 320px;
  margin: 0;
  overflow-x: hidden; }

.wrap {
  margin-left: 15px;
  margin-right: 15px; }

.btn {
  text-align: center;
  padding: 15px;
  border: none;
  border-radius: 6px;
  background-color: #e5e5e5;
  color: #949494;
  border: 2px solid transparent;
  transition: border-color .4s, background-color .4s, color .4s; }
  .btn:hover, .btn:focus {
    outline: none;
    border: 2px solid #f49530;
    background-color: white;
    color: black; }
  .btn:active {
    border: 2px solid #4ea31e; }

.timeline {
  margin: 0;
  padding: 0 8px;
  padding-bottom: 32px;
  padding-top: 32px;
  width: 100%;
  display: table;
  table-layout: fixed;
  box-sizing: border-box;
  counter-reset: li; }
  .timeline--label-left .timeline__item:first-child {
    width: 0; }
    .timeline--label-left .timeline__item:first-child::before {
      display: none; }
    .timeline--label-left .timeline__item:first-child .timeline__label {
      transform: none;
      left: 0; }
  .timeline--label-left .timeline__item:last-child .timeline__label {
    transform: none; }
  .timeline--label-left .timeline__item--active .timeline__progress {
    background-color: #4ea31e; }
    .timeline--label-left .timeline__item--active .timeline__progress::before {
      content: none; }
  .timeline--label-left .timeline__item--active::after {
    border-color: #f49530;
    background-color: #f49530; }
  .timeline--label-left .timeline__item
.timeline__label {
    left: auto;
    right: 0;
    transform: translateX(50%); }
  .timeline--under-line .timeline__label {
    bottom: -18px; }

.timeline__item {
  position: relative;
  display: table-cell;
  height: 2px; }
  .timeline__item::after, .timeline__item:first-child::before {
    content: '';
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(50%, -50%);
    box-sizing: border-box;
    width: 10px;
    height: 10px;
    border: 2px solid #e5e5e5;
    border-radius: 50%;
    transition: border-color .4s, background-color .4s; }
  .timeline__item:first-child::before {
    left: 0;
    transform: translate(-50%, -50%); }
  .timeline__item--active .timeline__progress {
    background-color: #f49530; }
    .timeline__item--active .timeline__progress::before {
      content: '';
      right: auto;
      left: -10px;
      background-color: #f49530;
      border-color: #f49530; }
  .timeline__item--traversed .timeline__progress {
    background-color: #4ea31e; }
  .timeline__item--traversed::after {
    border-color: #4ea31e;
    background-color: #4ea31e; }
  .timeline__item--traversed:first-child::before {
    border-color: #4ea31e;
    background-color: #4ea31e; }

.timeline__progress {
  height: 100%;
  margin-left: 7px;
  margin-right: 7px;
  background-color: #e5e5e5;
  transition: background-color .4s; }
  .timeline__progress::before {
    content: none;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(50%, -50%);
    box-sizing: border-box;
    width: 10px;
    height: 10px;
    border: 2px solid #e5e5e5;
    border-radius: 50%;
    transition: border-color .4s, background-color .4s; }

.timeline__label {
  position: absolute;
  display: block;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
  font-size: 12px;
  line-height: 12px;
  color: #949494;
  transition: color .4s;
  cursor: pointer; }
  .timeline__label:hover, .timeline__label:focus {
    outline: none;
    color: black; }
  .timeline__label::after {
    content: " " counter(li);
    counter-increment: li; }

.header {
  max-width: 450px;
  padding-top: 15px;
  margin-bottom: 40px; }
  .header ::after {
    content: "";
    display: block;
    clear: both; }

.header__btn {
  float: left;
  width: 190px;
  margin-top: 0;
  margin-left: 0;
  margin-right: 10px;
  margin-bottom: 10px; }

.main {
  margin-bottom: 40px; }

.main__title {
  display: block;
  margin: 0;
  margin-bottom: 30px;
  padding: 0; }

.footer {
  padding: 15px;
  padding-left: 0;
  padding-right: 0;
  background-color: #e5e5e5; }

.footer__link {
  display: block;
  width: 100px;
  margin-left: 0;
  margin-right: auto;
  margin-bottom: 10px;
  text-align: left;
  text-decoration: none;
  color: #949494;
  transition: color .4s; }
  .footer__link:hover, .footer__link:focus {
    outline: none;
    color: #f49530; }
